export default {
	data() {
		return {
			datas: [{//跳转的数据
				name: "关于商城",
				url: 'about'
			}, {
				name: "意见反馈",
				url: "feedback"
			}, {
				name: "协议规则",
				url: ""
			}, {
				name: "资质证件",
				url: ""
			}, {
				name: "用户协议",
				url: ""
			}, {
				name: "隐私政策",
				url: ""
			}]
		}
	},
	methods: {
		//返回上一层
		returni() {
			uni.navigateBack({
				delta: 1
			})
		},
		//个人资料
		personal() {
			uni.navigateTo({
				url: "/pages/mine/personal/personal?id=1"
			})
		},
		//收货地址
		receiving() {
			uni.navigateTo({
				url: "/pages/mine/address/address?id=1"
			})
		},
		//退出登录
		sign() {
			uni.showToast({
				title: '点击协',
				icon: 'none'
			})
		},
		//跳转指定页面
		navTop(e) {
			let address = e.currentTarget.dataset.url || e.target.dataset.url
			uni.navigateTo({
				url: "/pages/mine/" + address + "/" + address + "?id=1"
			})

		}
	},
}
